import React from 'react';
import { Row, Col } from 'react-bootstrap';
import styled from 'styled-components';
import pierwszy from "../../assets/pierwszy.png"
import image from "../../assets/MFA/offer-2.png"
import dot from "../../assets/MFA/dot.svg"

const Container = styled.div`
    height: 51em;
    
    background: #FCF5F0;

    max-width: 90em;
    justify-content: center;
    -webkit-justify-content: center;
    margin-left: auto;
    margin-right: auto;
   
`
const TextContainer = styled(Col)`
    padding-top: 1.1875em;
    padding-left: 0;
    padding-right: 0;
    margin-right: 0;
    
`

const ImageContainer = styled(Col)`
    padding-top: 1.1875em;
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
`
const StyledRow = styled(Row)`
    padding-left: 10.3125em;
    margin-right: 0;
    margin-left: 0;
`

const StyledSlogan = styled.div`
    width: 3.6666666666666665em;
    font-family: Messina Sans;
    font-style: normal;
    font-weight: normal;

    padding-left: 0.75em;
    padding-right: 0.75em;
    
    font-size: 0.75em;
    line-height: 2em;
    /* or 200% */
    text-align: center;
    display: flex;
    align-items: center;
    letter-spacing: 0.041666666666666664em;

    color: #FFFFFF;

    background: #FF6938;
    border-radius: 2.5em;
`

const StyledTitle = styled.div`

    
    max-width: 12.075em;

    margin-top: 0.5em;
    margin-bottom: 2.625rem;
    
    margin-right: 0;

    font-family: Messina Sans bold;
    font-style: normal;
    font-weight: bold;
    font-size: 2.5em;
    line-height: 1.2em;
    /* or 120% */
    letter-spacing: -0.00625em;

    color: #073233;
`

const StyledContent = styled.div`
    width: 27.8125em;
    font-family: Messina Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 1em;
    line-height: 1.5em;
    /* or 150% */
    letter-spacing: 0.00625em;
    margin-bottom: 2em;
    color: #17494D;
`

const StyledMoreabout = styled.button`
    
    font-family: Messina Sans semibold;
    font-style: normal;
    font-weight: 600;
    font-size: 1em;
    line-height: 1.5em;
    /* or 150% */
    display: flex;
    align-items: center;
    letter-spacing: 0.00625em;
    color: #073233;
    border: 0;
    background: none;
    padding: 0;
    margin-top: 2.0625em;
    > text {
        text-underline-offset: 2.5px;
        text-decoration: underline solid #FF6938;
        text-decoration-thickness: 3px;
        transition: 0.3s;
        &:hover {
            padding-right: 1em;
        }   

    }  
`

const StyledPointer = styled.div`
    margin-left: 0.7em;
    margin-bottom: 0.18em;
    font-size: 1.1em;
    line-height: 1.5em;
    letter-spacing: 0.00625em;
    color: #FF6938;
`

const Styledimg = styled.img`
    min-width: 44.375em;
    margin-bottom: 0;
    margin-right: 3.125em;

`

const StyledList = styled.div`
    max-width: 28.5625em;
`

const StyledDot = styled.img`
    margin: 0;
    margin-right: 0.8em;
    z-index: 1;
    position: absolute;
`

const ListElement = styled(Row)`
    margin: 0;
    margin-bottom: 0.4325em;
    
    padding: 0;

    font-family: Messina Sans;
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5em;
    letter-spacing: 0.00625em;
    text-align: left;
    color: #17494D;
    > div {
        padding-top: 0.09em;
        margin-left: 1.6875em;
    }


`

export const Offer = () => {
    return (
        <div style={{background: "#FCF5F0"}}>
        <Container>
            
            <StyledRow>
                <TextContainer>
                    <StyledSlogan>
                        MFA
                    </StyledSlogan>
                    <StyledTitle>
                    Convenient Authentication Methods
                    </StyledTitle>
                    <StyledContent>
                    Providing user-friendly authentication methods does not mean you’ll add unnecessary friction to your experience. With various login options provided, users get to choose the one that best fits their workflow.   
                    </StyledContent>
                    <StyledList>
                        <ListElement><StyledDot src={dot}/><div>SMS and email One Time Password (OTP)</div></ListElement>
                        <ListElement><StyledDot src={dot}/><div>Mobile time-based OTP token</div></ListElement>
                        <ListElement><StyledDot src={dot}/><div>Supports mobile fingerprint-sensor for FIDO <br />authentication</div></ListElement>
                        <ListElement><StyledDot src={dot}/><div>Supports Apple devices using Face ID authentication</div></ListElement>
                        <ListElement><StyledDot src={dot}/><div>Supports Palm and Finger Vein Scanner and even <br />offline login</div></ListElement>
                    </StyledList>
                    <StyledMoreabout>
                        <text>Learn about Multifactor Authentication</text>
                        <StyledPointer>   →</StyledPointer>
                    </StyledMoreabout>
                    
                </TextContainer>
                <ImageContainer>
                    <Styledimg src={image} />
                </ImageContainer>

            </StyledRow>
            
        </Container>
        </div>
    )
}